*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
#loginform {
  padding-bottom: 20px;
}
#loginform body {
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  background: #ededed;
}

#loginform input,
#loginform button {
  border: none;
  outline: none;
  background: none;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
}

#loginform .tip {
  font-size: 20px;
  margin: 40px auto 50px;
  text-align: center;
}

#loginform .cont {
  overflow: hidden;
  position: relative;
  width: 900px;
  height: 550px;
  margin: 0 auto;
  background: #fff;
}

#loginform .form {
  position: relative;
  width: 640px;
  height: 100%;
  transition: transform 1.2s ease-in-out;
  padding: 50px 30px 0;
}

#loginform .sub-cont {
  overflow: hidden;
  position: absolute;
  left: 640px;
  top: 0;
  width: 900px;
  height: 100%;
  padding-left: 260px;
  background: #fff;
  transition: transform 1.2s ease-in-out;
}
#loginform .cont.s--signup .sub-cont {
  transform: translate3d(-640px, 0, 0);
}

#loginform button {
  display: block;
  margin: 0 auto;
  width: 260px;
  height: 36px;
  border-radius: 30px;
  color: #fff;
  font-size: 15px;
  cursor: pointer;
}

#loginform .img {
  overflow: hidden;
  z-index: 2;
  position: absolute;
  left: 0;
  top: 0;
  width: 260px;
  height: 100%;
  padding-top: 360px;
}
#loginform .img:before {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 900px;
  height: 100%;
  background-image: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/142996/sections-3.jpg");
  background-size: cover;
  transition: transform 1.2s ease-in-out;
}
#loginform .img:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}
#loginform .cont.s--signup .img:before {
  transform: translate3d(640px, 0, 0);
}
#loginform .img__text {
  z-index: 2;
  position: absolute;
  left: 0;
  top: 50px;
  width: 100%;
  padding: 0 20px;
  text-align: center;
  color: #fff;
  transition: transform 1.2s ease-in-out;
}
#loginform .img__text h2 {
  margin-bottom: 10px;
  font-weight: normal;
}
#loginform .img__text p {
  font-size: 14px;
  line-height: 1.5;
}
#loginform .cont.s--signup .img__text.m--up {
  transform: translateX(520px);
}
#loginform .img__text.m--in {
  transform: translateX(-520px);
}
#loginform .cont.s--signup .img__text.m--in {
  transform: translateX(0);
}
#loginform .img__btn {
  overflow: hidden;
  z-index: 2;
  position: relative;
  width: 100px;
  height: 36px;
  margin: 0 auto;
  background: transparent;
  color: #fff;
  text-transform: uppercase;
  font-size: 15px;
  cursor: pointer;
}
#loginform .img__btn:after {
  content: "";
  z-index: 2;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border: 2px solid #fff;
  border-radius: 30px;
}
#loginform .img__btn span {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: transform 1.2s;
}
#loginform .img__btn span.m--in {
  transform: translateY(-72px);
}
#loginform .cont.s--signup .img__btn span.m--in {
  transform: translateY(0);
}
#loginform .cont.s--signup .img__btn span.m--up {
  transform: translateY(72px);
}

#loginform h2 {
  width: 100%;
  font-size: 26px;
  text-align: center;
}

#loginform label {
  display: block;
  width: 260px;
  margin: 25px auto 0;
  text-align: center;
}
#loginform label span {
  font-size: 12px;
  color: #817a7a;
  text-transform: uppercase;
}

#loginform input {
  display: block;
  width: 100%;
  margin-top: 5px;
  padding-bottom: 5px;
  font-size: 16px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  text-align: center;
}

#loginform .forgot-pass {
  margin-top: 15px;
  text-align: center;
  font-size: 12px;
  color: #cfcfcf;
}

#loginform .submit {
  margin-top: 40px;
  margin-bottom: 20px;
  background: #d4af7a;
  text-transform: uppercase;
}

#loginform .fb-btn {
  border: 2px solid #d3dae9;
  color: #8fa1c7;
}
#loginform .fb-btn span {
  font-weight: bold;
  color: #455a81;
}

#loginform .sign-in {
  transition-timing-function: ease-out;
}
#loginform .cont.s--signup .sign-in {
  transition-timing-function: ease-in-out;
  transition-duration: 1.2s;
  transform: translate3d(640px, 0, 0);
}

#loginform .sign-up {
  transform: translate3d(-900px, 0, 0);
}
#loginform .cont.s--signup .sign-up {
  transform: translate3d(0, 0, 0);
}

#loginform .icon-link {
  position: absolute;
  left: 5px;
  bottom: 5px;
  width: 32px;
}
#loginform .icon-link img {
  width: 100%;
  vertical-align: top;
}
#loginform .icon-link--twitter {
  left: auto;
  right: 5px;
}

#loginform .sign-up label {
  margin: 0px auto 0;
}
