:root {
  --price-tag-background: rgba(0, 0, 0, 0.3);
  --price-tag-height: 2rem;
}

.price-tag {
  background: rgba(0, 0, 0, 0.3);
  background: var(--price-tag-background);
  color: #fff;
  float: right;
  /* Center the price */
  align-items: center;
  display: flex;
  justify-content: center;
  /* Used to position the triangle */
  position: relative;
  /* Size */
  height: 2rem;
  height: var(--price-tag-height);
  width: 4rem;
  /* Spacing */
  padding: 0.25rem 0.5rem;
  margin: 0 1rem;
}

/* The triangle */

.price-tag::before {
  content: "";
  border-color: transparent rgba(0, 0, 0, 0.3) transparent transparent;
  border-color: transparent var(--price-tag-background) transparent transparent;
  border-style: solid;
  border-width: calc(2rem / 2)
    calc(2rem / 2) calc(2rem / 2) 0rem;
  border-width: calc(var(--price-tag-height) / 2)
    calc(var(--price-tag-height) / 2) calc(var(--price-tag-height) / 2) 0rem;
  /* Position */
  left: 0px;
  position: absolute;
  top: 0px;
  transform: translate(-100%, 0px);
}

/* The dot */

.price-tag::after {
  content: "";
  /* Make it like a cirle */
  background: #fff;
  border-radius: 9999rem;
  /* Position */
  left: 0;
  position: absolute;
  top: 50%;
  transform: translate(-0.5rem, -50%);
  /* Size */
  height: 0.5rem;
  width: 0.5rem;
}

